.card--findus {
    a {
        @include anchor-reset();

        &:visited {
            @include anchor-reset();
        }
    }
    .card__details {
        border-color: $color-gray1;
        border-style: $border-style-solid;
        border-width: $border-width-hairline;
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;

        .card__thumbnail {
            img {
                width: 100%;
            }
        }

        .card__details__text {
            padding-bottom: $measure--basic;
        }

        .card--findus__label--findus {
            @include smalltype();

            color: $color-gray7;
            font-style: italic;
            margin-top: 0;
        }

        .card--findus__details__address {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;

            .icon {
                color: $color-red50;
                padding-right: $measure--small2;
            }
        }
    }
}
