// <Desktop-specific layout - secondary navigation never present
main > .lifelong-learning {
    @include media('<small') {
        .key-information--lifelong-learning--bespoke {
            .key-information--lifelong-learning--bespoke__contact {
                @include standard-grid();
                @include standard-box();

                grid-template-rows: repeat(3, auto);
                justify-items: center;

                .key-information--lifelong-learning--bespoke__contact__header {
                    grid-column: 1 / span 24;
                    grid-row: 1;
                }

                .key-information--lifelong-learning--bespoke__contact__location {
                    grid-column: 1 / span 12;
                    grid-row: 2;
                }

                .key-information--lifelong-learning--bespoke__contact__details {
                    grid-column: 13 / span 12;
                    grid-row: 2;
                }

                .key-information--lifelong-learning--bespoke__contact__related-links {
                    grid-column: 1 / span 24;
                    grid-row: 3;
                }
            }
        }

        .key-information--lifelong-learning {
            @include standard-grid();
        }

        .lifelong-learning__tutors,
        .lifelong-learning__testimonials {
            @include standard-box();
        }

        .related-content--profiles {
            @include content-grid();

            .card {
                grid-column: 4 / -4;
            }
        }

        .lifelong-learning__video,
        .lifelong-learning__related-courses {
            @include default-margin();
        }
    }

    @include media('<between') {
        .key-information--lifelong-learning--bespoke {
            .key-information--lifelong-learning--bespoke__contact {
                @include standard-grid();

                grid-template-rows: repeat(4, auto);
                justify-items: center;

                .key-information--lifelong-learning--bespoke__contact__header {
                    grid-column: 1 / span 24;
                    grid-row: 1;
                }

                .key-information--lifelong-learning--bespoke__contact__location {
                    grid-column: 1 / span 24;
                    grid-row: 2;
                }

                .key-information--lifelong-learning--bespoke__contact__details {
                    grid-column: 1 / span 24;
                    grid-row: 3;
                }

                .key-information--lifelong-learning--bespoke__contact__related-links {
                    grid-column: 1 / span 24;
                    grid-row: 4;
                }
            }
        }
    }

    @include media('<tablet') {
        .related-content--profiles {
            display: initial;

            .card {
                // grid-column: 4 / -4;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);

        > .lifelong-learning {
            grid-column: 1 / span 24;

            .key-information--lifelong-learning--bespoke {
                .key-information--lifelong-learning--bespoke__contact {
                    @include standard-grid();
                    grid-template-rows: repeat(2, auto);

                    .key-information--lifelong-learning--bespoke__contact__header {
                        grid-column: 1 / span 24;
                        grid-row: 1;
                    }

                    .key-information--lifelong-learning--bespoke__contact__location {
                        grid-column: 1 / span 8;
                        grid-row: 2;
                    }

                    .key-information--lifelong-learning--bespoke__contact__details {
                        grid-column: 9 / span 8;
                        grid-row: 2;
                    }

                    .key-information--lifelong-learning--bespoke__contact__related-links {
                        grid-column: 17 / span 8;
                        grid-row: 2;
                    }
                }
            }

            .key-information--lifelong-learning {
                @include standard-grid();
            }

            .lifelong-learning__tutors,
            .lifelong-learning__testimonials {
                @include standard-grid();

                grid-template-rows: repeat(2, auto);

                h2 {
                    grid-column: 1 / span 24;
                }
            }

            .lifelong-learning__tutors {
                .related-content {
                    grid-column: 7 / span 12;
                }
            }

            .lifelong-learning__testimonials {
                .related-content {
                    grid-column: 1 / span 24;
                }

                .slider__controls {
                    grid-column: 1 / -1;
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .lifelong-learning {
                grid-column: 7 / span 18;

                .key-information--lifelong-learning--bespoke {
                    .key-information--lifelong-learning--bespoke__contact {
                        @include content-grid();
                        grid-template-rows: repeat(2, auto);

                        .key-information--lifelong-learning--bespoke__contact__header {
                            grid-column: 1 / span 18;
                            grid-row: 1;
                        }

                        .key-information--lifelong-learning--bespoke__contact__location {
                            grid-column: 1 / span 6;
                            grid-row: 2;
                        }

                        .key-information--lifelong-learning--bespoke__contact__details {
                            grid-column: 7 / span 6;
                            grid-row: 2;
                        }

                        .key-information--lifelong-learning--bespoke__contact__related-links {
                            grid-column: 13 / span 6;
                            grid-row: 2;
                        }
                    }
                }

                .key-information--lifelong-learning {
                    @include content-grid();
                }

                .lifelong-learning__tutors,
                .lifelong-learning__testimonials {
                    @include content-grid();

                    grid-template-rows: repeat(2, auto);

                    h2 {
                        grid-column: 1 / span 18;
                    }

                    .related-content {
                        grid-column: 4 / span 12;
                    }
                }

                .lifelong-learning__tutors {
                    .related-content {
                        grid-column: 4 / span 12;
                    }
                }

                .related-content--profiles {
                    @include content-grid();

                    .card {
                        grid-column: 4 / -4;
                    }
                }

                .lifelong-learning__testimonials {
                    .related-content {
                        grid-column: 1 / span 18;
                    }
                }
            }
        }
    }
}

// Generic styles
.lifelong-learning {
    @include banner($color-pink80);

    .banner-text {
        color: $color-purewhite;

        @include media('print') {
            color: $color-nearblack;
        }
    }

    .wrapper--key-information--lifelong-learning {
        background-color: $color-gray1;
        border-bottom: $border-width-default $border-style-solid $color-gray3;
        padding-bottom: $measure--small4;
    }

    .wrapper--lifelong-learning__introduction {
        background-color: $color-pink80;
        color: $color-purewhite;

        @include media('print') {
            color: $color-nearblack;
        }
    }

    .wrapper--lifelong-learning__higher-right-course {
        padding: $measure--threequarters;
        background-color: $color-gray1;
        border-top: $border-width-default solid $color-gray3;

        .tabs__panel:not([hidden]) {
            background: $color-gray13;
            .tabs__panel__content {
                @include media('>tablet', '<small') {
                    .tabs__panel__heading,
                    .tabs__panel__body {
                        grid-column: 1 / -1;
                    }
                }
            }
        }

        .tabs__links {
            justify-content: left;
        }

        .tabs .tabs__panel {
            border: $border-width-default $border-style-solid $color-gray3;
        }

        .wrapper--key-information--lifelong-learning {
            background-color: initial;
            border-bottom: initial;
            h2,
            h3,
            .key-information--lifelong-learning__dates {
                grid-column: 1/-1;
            }
        }
    }

    .lifelong-learning__introduction {
        @include default-margin();
        @include default-padding();

        padding-top: $measure--small4;
        padding-bottom: $measure--small4;
    }

    .lifelong-learning__selling-points__background {
        background: linear-gradient(
            180deg,
            $color-pink80 50%,
            $color-gray1 50%
        );
        background-repeat: no-repeat;
        border-bottom: $border-width-default $border-style-solid $color-gray3;
        padding-bottom: $measure--small3;
    }

    .lifelong-learning__selling-points {
        @include default-margin();
        @include default-padding();

        background-color: $color-purewhite;
        border: $border-style-solid $border-width-hairline $color-gray3;
        padding-bottom: $measure--small2;
        padding-top: $measure--small2;

        @include media('>=desktop') {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            padding-bottom: $measure--medium5;
            padding-top: $measure--medium5;
        }
    }

    .lifelong-learning__selling-points__card {
        display: flex;
        margin-bottom: $measure--small4;

        &:last-child {
            margin-bottom: 0;
        }

        @include media('>=desktop') {
            margin-bottom: 0;
            margin-right: $measure--small2;

            &:last-of-type {
                margin-right: 0;
            }
        }

        .icon {
            font-size: $measure--small5;
            min-width: $measure--medium3;
            margin-right: $measure--small4;

            @include media('>mobile') {
                min-width: $measure--medium2;
            }

            @include media('>between') {
                margin-right: $measure--basic;
            }
        }

        &:nth-child(1) {
            .icon {
                color: $color-green50;
            }
        }

        &:nth-child(2) {
            .icon {
                color: $color-teal50;
            }
        }

        &:nth-child(3) {
            .icon {
                color: $color-purple50;
            }
        }
    }

    .lifelong-learning__selling-points__title {
        font-weight: $font-weight-bold;
    }

    .key-information--lifelong-learning {
        grid-template-rows: repeat(2, auto);

        > h2,
        h3 {
            color: $color-pink80;
        }

        > h2 {
            grid-column: 2 / -2;
        }

        > h3 {
            grid-column: 1 / -1;
        }

        .key-information--lifelong-learning__icon {
            margin-right: $measure--basic;
        }

        > ul,
        > .pagination__wrapper {
            grid-column: 2 / -2;
            max-width: unset;
        }

        > .key-information--lifelong-learning__controls {
            grid-column: 2 / -2;
        }
        > ul > li {
            background-color: $color-purewhite;
            border: $border-width-hairline $border-style-solid $color-gray3;
            padding: 0 $measure--basic $measure--basic $measure--basic;
        }
        > ul > li > ul {
            margin-left: 0;
            margin-right: 0;
        }
    }

    .lifelong-learning__start-date__cta {
        text-align: center;

        .cta-block__cta > span {
            @include smalltype();
            display: block;
        }
    }

    .related-content__heading {
        @include heading--centered($color-pink80);
    }

    .lifelong-learning__related-courses {
        padding-bottom: $measure--medium8;
        padding-top: $measure--medium8;
    }

    .lifelong-learning__tutors__wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .lifelong-learning__tutors,
    .lifelong-learning__testimonials {
        padding-bottom: $measure--medium8;
        padding-top: $measure--medium8;

        h2 {
            @include heading--centered($color-pink80);
        }

        .related-content {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    .slider:not([data-small='true']) {
        margin-bottom: 0;
    }

    .lifelong-learning__testimonials {
        .related-content {
            .card__details {
                justify-content: center;
            }
        }
    }

    .lifelong-learning__video {
        background: $color-red5;
        margin-bottom: $measure--basic;
    }

    @include media('>=tablet') {
        .tabs .tabs__panel {
            border: $border-width-default $border-style-solid $color-gray3;
        }
    }

    .lifelong-learning__content {
        margin-top: $measure--basic;

        & + .lifelong-learning__contact {
            margin-top: $measure--small4;
        }

        .overview__subject-wrapper {
            margin-top: $measure--basic;
            padding: $measure--basic;
            background-color: $color-pink90;
            color: $color-purewhite;

            h3 {
                margin-top: 0;
            }

            .cta-block {
                a {
                    @include cta-basic-light-bg($color-gray3, $color-text);

                    &:focus,
                    &:active {
                        @include anchor-default-outline($color-outline--dark);
                    }
                }
            }
        }
    }

    .lifelong-learning__contact {
        @include default-padding();

        background-color: $color-pink90;
        color: $color-purewhite;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding-bottom: $measure--small4;
        padding-top: $measure--small4;

        @include media('<small') {
            @include default-margin();
        }

        @include media('>=between') {
            flex-direction: row;
        }

        .lifelong-learning__contact__heading {
            margin-top: 0;
            text-align: center;

            @include media('>=between') {
                align-self: center;
                text-align: left;
            }
        }

        .cta-block {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            max-width: unset;

            @include media('<between') {
                @include smalltype();
            }

            @include media('>=desktop') {
                flex-direction: row;
            }

            li {
                align-self: stretch;
                margin-top: $measure--basic;

                @include media('<desktop') {
                    margin-right: 0;
                }

                @include media('>=desktop') {
                    margin-right: $measure--basic;
                    margin-top: 0;
                }

                a {
                    @include cta-basic-light-bg($color-gray3, $color-text);

                    align-self: stretch;
                    display: flex;
                    justify-content: center;

                    &:focus,
                    &:active {
                        @include anchor-default-outline($color-outline--dark);
                    }

                    &,
                    &:active,
                    &:focus,
                    &:hover {
                        span {
                            .fa-phone {
                                @include fa-icon-rotate(90deg, 1);
                            }
                        }
                    }
                }
            }
        }
    }
}

@mixin display-tabs-content(
    $link-background: $color-purewhite,
    $panel-background: $color-gray1,
    $panel-border-color: $color-gray3
) {
    @include standard-box();

    background-color: $link-background;
    padding-top: $measure--small4;

    .tabs__links {
        display: none;
    }

    .tabs__panel {
        background-color: $panel-background;
        border-top: 0;
        padding: 0;

        @include media('>=tablet') {
            border: $border-width-default $border-style-solid
                $panel-border-color;
        }

        &[hidden] {
            display: block;
        }

        &:not([hidden]) {
            .tabs__panel__content {
                margin-left: 0;
                margin-right: 0;
            }
        }
    }

    .tabs__panel__heading {
        @include heading--centered($color-pink80);
    }
}

@include media('screen') {
    .js .lifelong-learning .modal__content,
    .js .lifelong-learning .modal__content a:not(:hover):not(:active) {
        color: $color-text;
    }

    .js .lifelong-learning .paginated-list .page ul,
    .js .lifelong-learning .paginated-list .page ol {
        list-style-type: none;
        margin-left: 0;
    }

    .js .lifelong-learning .modal__trigger {
        @include cta-basic-light-bg($color-red50, $color-purewhite);
        padding: $measure--threequarters $measure--small2;
    }

    .js .lifelong-learning__content .tabs__links {
        @include default-margin();
    }

    .js .lifelong-learning__content .accordion {
        @include standard-box();
    }

    .js .lifelong-learning__content > div:not(.tabs):not(.tabs--accordion) {
        @include display-tabs-content();
    }

    .js .lifelong-learning--bespoke .tabs {
        @include media('<tablet') {
            @include display-tabs-content();
        }
    }

    .js main {
        > .lifelong-learning {
            .lifelong-learning__content .tabs__panel:not([hidden]) {
                padding: 0;

                .tabs__panel__content {
                    @include standard-grid();

                    grid-template-rows: repeat(2, auto);

                    @include media('<=mobile') {
                        @include default-padding();
                    }

                    .tabs__panel__heading {
                        grid-row: 1;
                    }

                    .tabs__panel__body {
                        grid-row: 2;
                    }

                    .tabs__panel__heading,
                    .tabs__panel__body {
                        grid-column: 1 / span 24;

                        @include media('>mobile', '<=between') {
                            grid-column: 5 / span 16;
                        }

                        @include media('>between') {
                            grid-column: 7 / span 12;
                        }
                    }
                }
            }
        }

        > .navigation--secondary + .lifelong-learning {
            .lifelong-learning__content .tabs__panel:not([hidden]) {
                > .tabs__panel__content {
                    @include media('>=small') {
                        @include content-grid();

                        .tabs__panel__heading,
                        .tabs__panel__body {
                            grid-column: 4 / span 12;
                        }
                    }
                }
            }
        }
    }
}

.js main {
    > .lifelong-learning {
        .lifelong-learning__content > div:not(.tabs):not(.tabs--accordion) {
            > .tabs__panel {
                @include standard-grid();

                grid-template-rows: repeat(2, auto);
                padding: 0;

                @include media('<=mobile') {
                    @include default-padding();
                }

                .tabs__panel__heading {
                    grid-row: 1;
                }

                .tabs__panel__body {
                    grid-row: 2;
                }

                .tabs__panel__heading,
                .tabs__panel__body {
                    grid-row: 2;
                    grid-column: 1 / span 24;

                    @include media('>mobile', '<=between') {
                        grid-column: 5 / span 16;
                    }

                    @include media('>between') {
                        grid-column: 7 / span 12;
                    }
                }
            }
        }
    }

    > .navigation--secondary + .lifelong-learning {
        .lifelong-learning__content > div:not(.tabs):not(.tabs--accordion) {
            > .tabs__panel {
                @include media('>=small') {
                    @include content-grid();

                    .tabs__panel__heading,
                    .tabs__panel__body {
                        grid-column: 4 / span 12;
                    }
                }
            }
        }
    }
}

.lifelong-learning {
    .slider-block {
        grid-column: 1 / -1;
    }

    .slide__controls__progress__active {
        color: $color-pink90;
    }
}

.lifelong-learning {
    .slide__controls__progress__active {
        color: $color-pink90;
    }

    .swiper-slider-arrow {
        @include control-button($color-pink90);
    }
}
