// <Desktop-specific layout - secondary navigation never present
main > .landing--informational {
    @include media('<small') {
        .landing__standfirst,
        .landing__promotional {
            @include default-margin();
        }

        .landing__standfirst > .standfirst,
        .landing__promotional__flexible,
        .landing__promotional__heading,
        .landing__promotional__content,
        .landing__promotional__cta,
        .landing__pathway__content,
        .landing__profiles__flexible {
            @include default-padding();
        }

        .landing__related {
            @include default-margin();

            .related-content {
                li + li {
                    margin-top: $measure--small2;
                }
            }
        }
    }

    @include media('>=tablet', '<small') {
        .landing--informational__content__wrap {
            @include standard-grid();

            .landing__standfirst > .standfirst,
            .landing__promotional__flexible,
            .landing__promotional__heading,
            .landing__promotional__content,
            .landing__promotional__cta,
            .landing__pathway__content {
                grid-column: 6 / span 14;
                margin-left: 0;
                margin-right: 0;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .landing--informational {
            .landing__standfirst,
            .landing__promotional,
            .landing__related,
            .landing__pathway {
                @include standard-grid();
            }

            .landing__standfirst > .standfirst,
            .landing__promotional__flexible,
            .landing__promotional__heading,
            .landing__promotional__content,
            .landing__pathway__content {
                grid-column: 4 / span 18;
            }

            .landing__promotional__cta {
                grid-column: 4 / span 18;

                .cta-block {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;
                    justify-content: center;

                    li {
                        &:not(:last-of-type) {
                            margin-right: $measure--small2;
                        }
                    }
                }
            }

            .landing__pathway {
                .pathway {
                    grid-column: span 24;
                }
            }

            .landing__related {
                .landing__related__content,
                .landing__related__latest,
                .wrapper--landing__contact {
                    grid-column: span 24;
                }

                .landing__related__content,
                .landing__related__latest {
                    @include default-padding();
                }
            }
        }
    }

    @include media('>=desktop') {
        > .landing--informational {
            .landing__standfirst > .standfirst,
            .landing__promotional__flexible,
            .landing__promotional__heading,
            .landing__promotional__content,
            .landing__pathway__content {
                grid-column: 7 / span 12;
            }

            .landing__promotional__cta {
                grid-column: 5 / span 16;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + .landing--informational {
                .landing__standfirst,
                .landing__promotional,
                .landing__related,
                .landing__pathway {
                    @include content-grid();
                }

                .landing__related {
                    .landing__related__latest,
                    .landing__related__content,
                    .wrapper--landing__contact {
                        grid-column: span 18;
                    }
                }

                .landing__pathway {
                    .pathway {
                        grid-column: span 18;
                    }
                }

                .landing__standfirst > .standfirst,
                .landing__promotional__flexible,
                .landing__promotional__heading,
                .landing__promotional__content,
                .landing__promotional__cta,
                .landing__pathway__content {
                    grid-column: 4 / span 12;
                }
            }
        }
    }
}

// Generic styles
.landing--informational {
    .landing__promotional__flexible {
        margin-bottom: $measure--basic;

        h2:first-of-type {
            text-align: center;
        }
    }

    .landing__related {
        .wrapper--landing__contact {
            .landing__contact__heading {
                text-align: left;
            }
        }
    }

    .landing__pathway {
        margin-top: $measure--small2;
    }

    .landing__promotional__cta {
        .cta-block {
            margin-bottom: $measure--small2;
            margin-top: $measure--small2;
            max-width: unset;
        }
    }

    .wrapper--landing__contact {
        background-color: $color-gray1;
    }
}
