main {
    .home {
        .home__research__wrap {
            display: grid;
            @include x-column-grid(24);
            overflow: hidden;
            position: relative;
            padding-top: $measure--small3;
            @include media('>small') {
                padding-bottom: $measure--medium2;
            }

            h2 {
                grid-column: 4 / 22;
            }

            .home__research__content {
                grid-column: 4 / 13;
                @include media('<=small') {
                    margin-bottom: $measure--basic;
                }

                p {
                    @include betatype();
                    @include heading-font-features();
                    @include font-family('heading');
                }

                .home__research__content__links {
                    ul {
                        li {
                            a {
                                @include anchor-inline($color-gray10);
                                font-weight: $font-weight-medium;
                            }
                        }
                    }
                }
            }

            .home__research__card {
                grid-column: 13 / 23;
                overflow: hidden;

                ul {
                    margin-left: 0;
                }
            }

            .home__research__link {
                grid-column: 4 / 22;
            }
        }

        @include media('<=desktop') {
            .home__research__wrap {
                h2 {
                    grid-column: 3 / 23;
                }

                .home__research__content {
                    grid-column: 3/ 13;
                }

                .home__research__card {
                    grid-column: 13 / 23;
                    overflow: hidden;
                }

                .home__research__link {
                    grid-column: 3 / 23;
                }
            }
        }

        @include media('<=small') {
            .home__research__wrap {
                .home__research__content,
                .home__research__card,
                .home__research__link,
                h2 {
                    grid-column: 5/ 21;
                }
            }
        }

        @include media('<=between') {
            .home__research__wrap {
                .home__research__content,
                .home__research__card,
                h2 {
                    grid-column: 4/ 22;
                }
            }
        }

        @include media('<=tablet') {
            .home__research__wrap {
                .home__research__card {
                    grid-column: 4/ 22;
                }
            }
        }

        @include media('<=mobile') {
            .home__research__wrap {
                .home__research__content,
                .home__research__card,
                h2 {
                    grid-column: 3/ 23;
                }
                .home__research__card {
                    .home__research__link {
                        margin-left: 0;
                    }
                }
            }
        }
    }
}
