.finder__results,
.search__results {
    .card--contact {
        .card__details {
            border-color: $color-gray1;
            border-style: $border-style-solid;
            border-width: $border-width-hairline;
            display: block;

            @include media('<=between') {
                background-color: $color-gray1;
            }

            .card__details__text {
                padding-bottom: $measure--basic;
            }

            .card__type {
                color: $color-red50;
            }
        }
    }
}

.related-content {
    .card.card--contact {
        margin-top: 0;
        margin-bottom: 0;
        padding-bottom: $measure--small2;

        & + .card--contact {
            border-top-style: $border-style-solid;
            border-top-color: $color-nearblack;
            border-top-width: $border-width-hairline;
            padding-top: $measure--small2;
        }

        .card__details {
            .card__details__text {
                padding-bottom: $measure--basic;
            }
        }
    }
}

.card--contact {
    .card__details {
        .card__tag {
            .icon {
                color: $color-red50;
            }
        }
    }

    .wrapper--card--contact__content {
        display: flex;
        flex-wrap: nowrap;

        @include media('<small') {
            flex-direction: column;
        }

        @include media('>=small') {
            flex-direction: row;

            .card--contact__content {
                margin-right: $measure--small2;
            }
        }
    }

    .wrapper--card--contact__address {
        @include content-style();

        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        margin-top: $content-element-margin;

        address {
            .card--contact__addressline {
                display: block;
            }
        }

        .icon {
            line-height: $line-height-ratio--body;
            padding-top: $measure--quarter;
        }
    }
}
