// <Desktop-specific layout - secondary navigation never present
main > .landing--functional {
    @include media('<small') {
        .wrapper--landing--functional__content,
        .landing--functional__video {
            @include default-margin();
        }

        .landing--functional__content,
        .landing__contact {
            @include default-padding();
        }
    }

    @include media('>=tablet', '<small') {
        .wrapper--landing--functional__content {
            @include standard-grid();

            margin-left: 0;
            margin-right: 0;

            .landing--functional__content,
            .wrapper--finder--mini,
            .landing--functional__finder__links__content {
                grid-column: 4 / span 18;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .landing--functional {
            .wrapper--landing--functional__content,
            .landing--functional__video,
            .landing__contact {
                @include standard-grid();
            }

            .landing--functional__content {
                grid-column: 6 / span 14;
            }

            .wrapper--finder--mini,
            .landing--functional__finder__links__content {
                grid-column: 5 / span 16;
            }

            .landing--functional__video {
                .embedded-video {
                    grid-column: span 24;
                }
            }
        }
    }

    @include media('>=desktop') {
        > .landing--functional {
            .landing--functional__content {
                grid-column: 7 / span 12;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + .landing--functional {
                .wrapper--landing--functional__content,
                .landing--functional__video {
                    @include content-grid();
                }

                .landing--functional__content {
                    grid-column: 4 / span 12;
                }

                .wrapper--finder--mini,
                .landing--functional__finder__links__content {
                    grid-column: 3 / span 14;
                }

                .landing--functional__video {
                    .embedded-video {
                        grid-column: span 18;
                    }
                }
            }
        }
    }
}

// Generic styles
.landing--functional {
    .landing--functional__gradient {
        background-image: linear-gradient(
            to top,
            $color-purewhite,
            $color-gray1
        );
    }

    .landing--functional__content {
        > h2:first-of-type {
            text-align: center;
        }
    }

    .landing--functional__video {
        .embedded-video {
            padding-left: 0;
            padding-right: 0;

            .embedded-video-details {
                @include media('<=tablet') {
                    display: block;
                }
            }

            .embedded-video-details__heading {
                @include media('<=tablet') {
                    text-align: center;
                }
            }
        }

        .embedded-video-details__description {
            max-width: none;
        }
    }

    .wrapper--landing__contact {
        background-color: $color-pink80;
        color: $color-purewhite;

        .landing__contact__heading {
            border-bottom-color: $color-pink70;
        }

        .landing__contact {
            .related-content {
                .card__details {
                    .card__tag {
                        a {
                            @include anchor-inline(
                                $color-gray4,
                                $color-gray4,
                                $color-gray11,
                                $color-outline--dark
                            );
                        }
                    }
                }

                .card--contact {
                    .card__details {
                        .card__tag {
                            .icon {
                                color: $color-purewhite;
                            }
                        }
                    }
                }

                li + li {
                    border-top-color: $color-pink70;
                }
            }
        }

        .landing__contact__location {
            .card--findus {
                a {
                    &:focus {
                        outline-color: $color-purewhite;
                    }
                }

                .card--findus__content__name {
                    @include anchor-inline(
                        $color-gray4,
                        $color-gray4,
                        $color-gray11,
                        $color-outline--dark
                    );
                }

                .card--findus__content__address {
                    color: $color-purewhite;

                    .icon {
                        color: $color-purewhite;
                    }
                }
            }
        }
    }
}
