main {
    .home {
        @include media('>=small') {
            grid-column: 1/-1;
        }

        .section--wrapper--homepage-banner {
            @include media('<tablet') {
                @include default-padding;
            }
        }

        .banner--home {
            @include standard-grid;
            position: relative;
            overflow: hidden;
            @include media('<small') {
                padding: $measure--small4 0 $measure--basic;
            }
            &:before {
                // Base SVG ribbon rules
                @include media('>=tablet') {
                    content: '';
                    background: url(https://www.city.ac.uk/__data/assets/file/0006/537486/ribbon.svg)
                        0 0 no-repeat;
                    position: absolute;
                    z-index: z-index('default');
                    // Transform and position SVG ribbon to near-horizontal
                    height: 100%;
                    transform: rotate(3deg) scale(1.8, 1.2) skew(6deg, -12deg);
                    width: 104%;
                    right: 18%;
                    top: 24%;
                }
                // Transform and position SVG ribbon to near-vertical
                @include media('>=small') {
                    z-index: z-index('above');
                    width: 103%;
                    height: 100%;
                }
                @include media('>=small', '<desktop') {
                    transform: rotate(84deg) scale(-0.75, -1) skew(0deg, 5deg);
                    right: 9%;
                    top: 10%;
                }
                @include media('>=desktop') {
                    transform: rotate(84deg) scale(-0.95, -1.05)
                        skew(0deg, 4deg);
                    right: 12.5%;
                    top: 14%;
                }
            }
            color: $color-purewhite;

            .banner__intro,
            .banner__video {
                grid-column: 1/-1;
                @include media('>=tablet', '<small') {
                    grid-column: 3/-3;
                }
                @include media('>=between', '<small') {
                    grid-column: 4/-4;
                }
            }

            .banner__intro {
                @include media('>=small') {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    z-index: z-index('inpage');
                }

                @include media('<desktop') {
                    .cta-block__cta {
                        margin-bottom: $measure--basic;
                    }
                }

                
                @include media('>=small', '<desktop') {
                    grid-column: 2/11;
                }
                @include media('>=desktop') {
                    grid-column: 2/10;
                }
                .banner__intro__heading {
                    @include media('>=small') {
                        padding-right: $measure--basic;
                    }
                }
                .banner__intro__subheading {
                    margin: $measure--small2 0 $measure--basic;
                }
                .primary-cta-arrow--bright {
                    @include media('<small') {
                        margin-bottom: $measure--small2;
                    }
                    &:focus {
                        outline-color: $color-purewhite;
                    }
                }
            }

            .banner__video {
                @include media('>=small') {
                    z-index: z-index('default');
                }
                @include media('>=small', '<desktop') {
                    grid-column: 11/-1;
                }
                @include media('>=desktop') {
                    grid-column: 10/-1;
                }
                margin-top: 0;
                &.embedded-video {
                    padding: 0;
                    display: block;
                    figcaption {
                        color: $color-purewhite;
                        text-align: right;
                        margin-top: $measure--quarter;
                    }
                }
                &.embedded-video--youtube {
                    @include media('>=small') {
                        display: none;
                    }
                }
                &.embedded-video--autoplay {
                    @include media('<small') {
                        display: none;
                    }
                }
            }
        }

        .wrapper--embedded-video--autoplay__caption {
            @include standard-grid;
            .embedded-video--autoplay__caption {
                grid-column: -10/-1;
                color: $color-gray7;
                text-align: right;
                margin-top: $measure--quarter;
                @include media('<small') {
                    display: none;
                }
            }
        }

        .banner__ribbon {
            position: absolute;
            transform: rotate(280deg) scale(-1, -1);
        }
    }
}
