main {
    .home {
        .home__whats-happening {
            display: grid;
            @include x-column-grid(24);
            @include media('<small') {
                @include default-padding;
            }
            margin-bottom: $measure--small4;

            h2 {
                text-align: center;
                margin-top: 0;
                grid-column: 1/-1;
            }

            .related-content {
                @include x-column-grid(24);
                grid-column-gap: $measure--small2;
                grid-column: 1/-1;

                @include media('>=mobile') {
                    grid-column: 4/-4;
                }

                @include media('>=tablet') {
                    grid-column: 5/-5;
                }

                @include media('>=between') {
                    grid-column: 6/-6;
                }

                @include media('>=small') {
                    display: grid;
                    grid-column: 2/-2;
                    grid-template-columns: repeat(3, 1fr);
                }
            }
        }
    }
}
