main {
    @include media('>=small') {
        .content-hub {
            grid-column: 1 / span 24;
        }

        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .content-hub {
                grid-column: 7 / span 18;
            }
        }
    }
}

.content-hub__block-one {
    grid-template-rows: auto;
    background-image: linear-gradient(to top, $color-purewhite, $color-gray1);

    > .content {
        grid-area: main;
        margin: 0;
        display: grid;
        grid-template-rows: auto;

        @include media('<=mobile') {
            grid-template-columns: repeat(6, 1fr);
        }

        @include media('>mobile') {
            grid-template-columns: repeat(12, 1fr);
        }

        @include media('>between') {
            @include standard-grid();
        }
    }
}

