.content-hub {
    .banner,
    .banner-text {
        .cta-block {
            display: flex;
            grid-column: 1 / span 24;
            justify-content: center;
            margin-top: $measure--small4;
            max-width: unset;
            text-align: center;

            .cta-block__cta {
                a {
                    @include cta-outlined-icon-dark-bg();
                }
            }
        }
    }
}

.content-hub__block-one .banner {
    @include standard-grid();

    grid-column: 1 / -1;
}

.content-hub .banner__standfirst {
    grid-column: 1 / -1;
    background-color: $color-red70;
    color: $color-purewhite;

    @include media('>=tablet') {
        grid-column: 3 / -3;
    }

    h1 {
        text-align: center;
    }

    .standfirst {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
}

.content-hub__block-one .video-finder-wrapper {
    @include standard-grid();
    @include media('<=tablet') {
        padding: $measure--small2 0;
        @include default-margin();
    }
    grid-column: 1/-1;
    padding-bottom: $measure--medium3;
    margin-top: 0;
}

.content-hub__block-one .embedded-video {
    grid-column: 3/-3;
    padding: $measure--small4 0;
    @include media('<=tablet') {
        grid-column: 1/-1;
        padding: 0;
    }
}

.content-hub__block-one .embedded-video .youtube-video {
    padding-left: 0;
}

.content-hub__block-one .explore {
    grid-column: 3/-3;

    @include media('<=tablet') {
        grid-column: 1/-1;
        padding: $measure--small4 0;
    }

    .explore__heading {
        text-align: center;
        margin: 0;
    }
}

.content-hub__block-one .cta-like-anchor-block {
    .cta-like-anchor {
        a {
            justify-content: center;
            text-align: center;
            &:hover,
            &:active {
                color: $color-nearblack;
            }
        }
    }
}

.content-hub__block-two {
    margin-bottom: $measure--basic;
    margin-left: 0;
    margin-right: 0;
}

.content-hub__block-two {
    .careers {
        background-color: $color-brown10;
        padding: $measure--basic 0;
        margin-bottom: $measure--medium3;

        @include media('>=tablet') {
            @include standard-grid;
        }
        @include media('>=between') {
            margin-bottom: 0;
        }

        .careers__intro {
            @include media('<tablet') {
                @include default-margin;
            }
        }

        .pathway-cards {
            @include media('<tablet') {
                @include default-margin;
            }

            .pathway {
                margin-top: initial;
                padding-top: initial;

                .pathway__cards {
                    padding-left: 0;
                    padding-right: 0;
                }
            }
        }

        .card--pathway {
            a {
                @include media('>tablet') {
                    @include media('hover') {
                        &:hover {
                            background-color: $color-purewhite;
                        }
                    }

                    &:active,
                    &:focus {
                        background-color: $color-purewhite;
                    }

                    .card--pathway__content {
                        min-height: 15rem;
                    }
                }
                @include media('<=small') {
                    background-color: $color-purewhite;
                }
            }
        }
    }
}

.content-hub__block-two .careers-accordions {
    background-color: $color-brown10;
    position: relative;
    overflow: hidden;

    @include media('>=tablet') {
        @include standard-grid;
    }

    @include angled-edge(
        $background-color: $color-gray1,
        $pseudo: 'before',
        $skew: -8.5deg
    );

    @include angled-edge(
        $background-color: $color-gray1,
        $pseudo: 'after',
        $skew: 8.5deg
    );

    &:before {
        content: '';
        z-index: z-index('default');
        display: block;
        grid-column: 1 / -1;
        margin: 0 0 $measure--basic 0;
        top: initial;
        bottom: -$measure--medium11;
        height: $measure--small4;
        background-image: linear-gradient(to left, $color-red50, $color-red70);
    }

    &:after {
        content: '';
        z-index: z-index('default');
        display: block;
        grid-column: 1 / -1;
        margin: 0 0 $measure--basic 0;
        bottom: -$measure--basic;
        height: $measure--medium3;
        background-image: linear-gradient(to right, $color-red50, $color-red70);
    }

    .careers-accordions--content {
        @include media('>=tablet') {
            grid-column: 3 / 23;
        }

        @include media('<=tablet') {
            @include default-margin;
        }
    }

    & > h2 {
        margin-top: $measure--small4;

        @include media('>=tablet') {
            grid-column: 3 / 23;
        }

        @include media('<=tablet') {
            @include default-margin;
        }
    }

    .accordion {
        position: relative;
        z-index: z-index('above');
        margin-bottom: $measure--small4;

        @include media('>=tablet') {
            grid-column: 3 / 23;
        }

        @include media('<=tablet') {
            @include default-margin;
        }
    }

    .accordion__heading {
        button {
            background: $color-purewhite;
            color: $color-nearblack;
        }

        &[data-open='true'] {
            button {
                background: $color-pink90;
                color: $color-purewhite;
                border-color: $color-purewhite;

                &:focus {
                    border-color: $color-purewhite;
                }

                @include media('hover') {
                    &:hover {
                        background: $color-pink90;
                        color: $color-purewhite;
                    }
                }
            }

            & + .accordion__body {
                margin-bottom: $measure--basic;
                border-color: $color-purewhite;
            }

            & + .accordion__body:last-child {
                margin-bottom: 0;
            }
        }
    }

    .accordion__body {
        background: $color-pink90;
        color: $color-purewhite;
    }

    .pathway {
        .pathway__cards {
            .card--pathway a {
                @include pathway-card(
                    $card-text-color: $color-purewhite,
                    $card-text-hover-color: $color-nearblack,
                    $card-background-color: transparent,
                    $card-background-hover-color: $color-purewhite,
                    $card-heading-color: $color-purewhite,
                    $section-title-color: $color-purewhite,
                    $border-color: $color-pink80,
                    $outline-color: $color-purewhite
                );

                @include media('hover') {
                    &:hover {
                        &:visited {
                            .card--pathway__content__heading,
                            p,
                            ul {
                                color: $color-nearblack;
                            }
                        }
                    }
                }

                @include media('<=small') {
                    color: $color-nearblack;

                    .card--pathway__content__heading,
                    p,
                    ul {
                        color: $color-nearblack;
                    }

                    .card--pathway__content__heading__text {
                        border-bottom-color: $color-nearblack;
                    }

                    &:visited {
                        .card--pathway__content__heading,
                        p,
                        ul {
                            color: $color-nearblack;
                        }
                        .card--pathway__content__heading__text {
                            border-bottom-color: $color-nearblack;
                        }
                    }
                }

                .card--pathway__content {
                    min-height: 15rem;
                }
            }

            a.card--pathway__content--border {
                border-top: $measure--half solid $color-pink80;
                max-width: unset;
            }
        }
    }
}

.content-hub__block-two .careers__intro {
    @include media('>=tablet') {
        grid-column: 3 / 23;
    }
}

.content-hub__block-two .pathway-cards {
    @include media('>=tablet') {
        grid-column: 5 / 21;
    }
    @include media('>between') {
        grid-column: 3 / 23;
    }
    @include media('>desktop') {
        grid-column: 7 / 19;
    }
}

