////
/// Footer - This is for the reusable footer element on course pages paint layouts asset id 503750
/// @group patterns
////

.course-footer {
    @include default-padding();
    padding-top: $heading-margin;
    padding-bottom: $measure--small2;

    h2 {
        border-bottom: $border-width-hairline $border-style-solid $color-gray4;

        &:not(:first-child) {
            margin-top: $measure--small4;
        }
    }

    .related-content {
        justify-content: left;
        .card.card--contact {
            padding-top: 0;
            padding-bottom: 0;

            &:not(:first-child) {
                .card__details {
                    .card__details__text {
                        padding-top: 0;
                    }
                }
            }

            .card__details__text {
                padding-left: 0;
            }
            & + .card--contact {
                padding-top: 0;
                border-top: 0;
            }
        }
    }
    .related-content .card.card--contact + .card--contact {
        border-top: none;
    }

    &.background--pink80 {
        color: $color-purewhite;

        h2 {
            border-bottom: $border-width-hairline $border-style-solid
                $color-pink70;
        }

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-gray11,
                $color-outline--dark
            );
        }

        .card .card__details .card__tag .icon {
            color: $color-purewhite;
        }
    }

    .useful-links {
        flex-basis: 33%;

        @include media('<between') {
            margin-top: $measure--basic;
        }
    }
}
