// <Desktop-specific layout - secondary navigation never present
main > .documentation {
    @include media('<small') {
        @include default-margin();
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .documentation {
            grid-column: 6 / span 14;

            &.documentation--fullwidth {
                grid-column: 1 / span 24;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present

main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .documentation {
                grid-column: 7 / span 18;

                &.documentation--fullwidth {
                    grid-column: 7 / span 18;
                }
            }
        }
    }
}

.article--fullwidth {
    main {
        @include media('>=small') {
            .navigation--secondary {
                & + .documentation {
                    grid-column: 7 / -1;
                }
            }
        }
    }
}
// Generic styles
.documentation {
    .content {
        margin-top: $measure--small2;
    }
}

.slider.doc-slider {
    list-style: none;
    margin: 0 auto;

    li {
        height: $measure--large2;
        width: 100%;

        border: $border-width-default $border-style-solid $color-nearblack;
    }
}
