// <Desktop-specific layout - secondary navigation never present
main > .event {
    @include media('<small') {
        .wrapper--event__content,
        .wrapper--event__contact,
        .event__formassembly__form {
            @include default-margin();
        }

        .event__content,
        .event__contact {
            @include default-padding();
        }
    }

    @include media('>=tablet', '<small') {
        .wrapper--event__content {
            @include standard-grid();

            margin-left: 0;
            margin-right: 0;

            .event__content {
                grid-column: 4 / span 18;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        @include standard-box();
        @include standard-grid();

        > .event {
            grid-column: span 24;

            .wrapper--event__content,
            .event__contact,
            .event__top__gradient {
                @include standard-grid();
            }

            .banner-text,
            .banner--image-generic {
                grid-column: span 24;
            }

            .wrapper--event__info {
                grid-column: 5 / span 16;

                .event__info {
                    @include x-column-grid(16);

                    display: grid;
                }
            }

            .event__content {
                grid-column: 6 / span 14;
            }

            .event__contact {
                @include standard-grid();

                .event__contact__details {
                    grid-column: 2 / span 9;
                }

                .event__contact__location {
                    grid-column: 11 / span 13;
                }

                .event__contact__details:only-child,
                .event__contact__location:only-child {
                    grid-column: 2 / span 22;
                }
            }
        }
    }

    @include media('>=desktop') {
        > .event {
            .event__content {
                grid-column: 7 / span 12;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .event {
                grid-column: 7 / span 18;

                .wrapper--event__content,
                .event__top__gradient {
                    @include content-grid();
                }

                .banner-text,
                .banner--image-generic {
                    grid-column: span 18;
                }

                .wrapper--event__info {
                    grid-column: 2 / -2;
                }

                .event__content {
                    grid-column: 4 / span 12;
                }

                .event__contact {
                    @include content-grid();

                    .event__contact__details {
                        grid-column: 2 / span 7;
                    }

                    .event__contact__location {
                        grid-column: 9 / span 9;
                    }

                    .event__contact__details:only-child,
                    .event__contact__location:only-child {
                        grid-column: 2 / span 16;
                    }
                }
            }
        }
    }
}

@include media('>tablet') {
    .event {
        .banner-text {
            padding-bottom: $measure--medium7;
        }

        .wrapper--event__info {
            position: relative;
        }
    }
}

.event {
    .event__top__gradient {
        background-image: linear-gradient(
            to top,
            $color-purewhite,
            $color-gray1
        );

        .background--purple80 {
            background-color: $color-purple80;
        }

        .background--pink80 {
            background-color: $color-pink80;
        }

        .background--blue80 {
            background-color: $color-blue80;
        }

        .background--red80 {
            background-color: $color-red80;
        }

        .banner--image-generic {
            padding-bottom: $measure--medium7;
        }
    }

    .embedded-video,
    .carousel-gallery-default {
        margin: $measure--threequarters 0;
    }

    .footer-cards {
        grid-column: 1 / -1;
        color: $color-purewhite;

        h2 {
            border-bottom: $border-width-hairline solid $color-red30;
        }

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-gray11,
                $color-outline--dark
            );
        }
    }
}

.event__dates {
    @include smalltype();

    color: $color-gray8;

    span {
        font-weight: $font-weight-medium;
    }
}

.wrapper--event__info {
    padding: $measure--basic;
    background-color: $color-purewhite;
    margin-bottom: $measure--basic;
    @include box-shadow();

    @include media('<tablet') {
        padding: $measure--basic 0;

        .event__info {
            @include default-margin;
        }
    }

    @include media('>=tablet', '<=small') {
        @include standard-box();
    }

    @include media('>=tablet') {
        margin-top: -$measure--small4;
    }

    .icon {
        margin-right: $measure--half;
        @include smalltype();
        color: $color-red50;
    }
}

.event__info__details {
    grid-column: 1 / span 10;

    @include media('>tablet') {
        display: grid;
        @include x-column-grid(10);

        .event__info__details__top {
            grid-column: 1 / span 6;
        }

        .event__info__details__bottom {
            grid-column: 7 / span 4;
        }
    }

    p {
        margin-top: 0;
    }

    ul {
        margin-top: 0;
        margin-left: 0;
        list-style: none;

        li {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            margin-top: 0;
            margin-bottom: $measure--basic;
        }
    }
}

.event__info__cta {
    grid-column: 11 / span 6;
}

.event__info__cta__top {
    @include media('>=small') {
        grid-column: 4/ -1;

        li {
            a,
            button {
                width: 100%;
                span {
                    width: 100%;
                    text-align: center;
                }
            }
        }
    }

    @include media('<small') {
        margin: $measure--basic 0;
    }

    @include media('<=mobile') {
        li {
            button {
                width: 100%;
            }
        }
    }

    a:first-child {
        margin-bottom: $measure--half;
    }

    ul {
        list-style: none;
        margin-top: 0;
        margin-left: 0;
    }

    li {
        margin-top: 0;

        @include media('>mobile', '<small') {
            display: inline;
            margin-right: $measure--basic;
        }
    }
}

.event__info__cta__top--twocta {
    ul {
        @include media('<small') {
            display: flex;
        }

        li {
            a {
                padding: 0.5rem 0.75rem;
            }
        }
    }
}

.event__info__text {
    grid-column: 1/ -1;
    margin: $measure--half 0;

    @include media('>=small') {
        display: flex;
        justify-content: flex-end;
    }

    .event__info__text--recurring {
        display: inline;

        .fa-redo {
            margin-right: $measure--half;
        }
    }
}

// standfirst

.event__standfirst {
    @include gammatype();
    @include font-family('heading');
    font-weight: $font-weight-light;
}

//event video

.event {
    .embedded-video {
        padding: 0;
    }
}

//event social card

.event__social-cards {
    margin-top: 0;
    margin-left: 0;
    max-width: unset;
}

//event agenda card

.event__agenda {
    list-style-type: none;
    margin-top: 0;
    margin-left: 0;
    max-width: unset;
}

.event__agenda--card {
    background-color: $color-purewhite;
    padding: $measure--basic;
    display: flex;
    @include box-shadow();
}

.event__agenda--card__icon {
    min-width: $measure--medium3;

    span {
        background-color: $color-red50;
        color: $color-purewhite;
        padding: $measure--half;
        @include smalltype();
    }
}

.event__agenda--card__details--time {
    color: $color-gray7;
    @include smalltype();
}

//speaker
.event__speakers {
    list-style-type: none;
    margin-top: 0;
    margin-left: 0;

    li:after {
        content: '';
        display: inline-block;
        width: 100%;
        height: $measure--basic;
        border-bottom: $border-width-default dotted $color-gray7;
    }
}

.event__speakers__name {
    @include gammatype();
}

.event__speakers__name,
.event__speakers__description {
    display: block;
}

.event__speakers__description {
    color: $color-gray7;
}

.event__speakers__details {
    display: flex;
    @include smalltype();
    span {
        @include alphatype();
        margin-right: $measure--half;
    }
}

//sponsor

.event__sponsors {
    margin-top: 0;
    margin-left: 0;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    justify-content: space-between;

    li {
        margin-top: $measure--half;
        width: 40%;
    }
    @include linked-img();
}

//event tags

.event__tags {
    h2 {
        font-family: $font-family-plex-sans;
        color: $color-gray7;
        margin-bottom: $measure--basic;
        @include smalltype();
    }

    .cta-block__cta {
        margin: 0 $measure--small2 $measure--threequarters 0;
    }
}

//event guide

.event__guide {
    margin: $measure--basic 0;
}

.event_related-events {
    grid-column: 1/ -1;
    background-color: $color-gray1;

    .event_related-events__wrapper {
        padding: $measure--basic 0;
        @include default-margin();
    }

    h2 {
        text-align: center;
    }

    .related-content {
        flex-wrap: wrap;

        @include media('>=small') {
            //@include standard-box();
        }

        @include media('<small') {
            @include default-margin;
        }

        @include media('>mobile') {
            display: flex;
            justify-content: space-between;
        }
    }

    li {
        @include media('>small') {
            width: 32%;
        }
        @include media('>=mobile', '<=small') {
            width: 47%;
        }
    }
}

.wrapper--event__social-icons {
    margin-bottom: $measure--small4;
    margin-left: 0;
    margin-right: 0;
    margin-top: $measure--small4;
}

.event__social-icons {
    border-bottom: $border-width-hairline $border-style-solid $color-gray2;
    border-top: $border-width-hairline $border-style-solid $color-gray2;
    position: relative;
}

.wrapper--event__contact {
    margin-top: $measure--basic;
    padding-bottom: $measure--small4;
    padding-top: $measure--basic;

    .event__contact__heading {
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-hairline;
        padding-bottom: $measure--half;
        text-align: left;
    }

    .event__contact {
        .related-content {
            align-items: flex-start;
            display: inline-flex;
            flex-direction: column;

            .card--contact {
                background: initial;
                box-shadow: initial;

                .card__details__text {
                    padding-left: 0;
                    padding-right: 0;
                }
            }

            li + li {
                @include media('<small') {
                    border-top-style: $border-style-solid;
                    border-top-width: $border-width-hairline;
                    padding-top: $measure--small4;
                }
            }
        }
    }
}

.wrapper--event__contact {
    background-color: $color-red80;
    color: $color-purewhite;

    .event__contact__heading {
        border-bottom-color: $color-red70;
    }

    .event__contact {
        .related-content {
            .card__details {
                .card__tag {
                    a {
                        @include anchor-inline(
                            $color-gray4,
                            $color-gray4,
                            $color-gray11,
                            $color-outline--dark
                        );
                    }
                }
            }

            .card--contact {
                .card__details {
                    .card__tag {
                        .icon {
                            color: $color-purewhite;
                        }
                    }
                }
            }

            li + li {
                border-top-color: $color-red70;
            }
        }
    }
}
