// <Desktop-specific layout - secondary navigation never present
main > .listing {
    @include media('<small') {
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .listing {
            grid-column: 1 / span 24;

            .finder {
                @include x-column-grid(24);

                display: grid;

                .finder__query,
                .wrapper--finder__filters--desktop,
                .finder__results {
                    grid-column: 3 / -3;
                }

                .finder__query {
                    margin-left: 0;
                    margin-right: 0;
                }

                .wrapper--finder__filters--desktop .finder__filters__heading {
                    margin: 0;
                    padding-left: 0;
                    padding-right: 0;
                }

                .wrapper--finder__filters--desktop
                    .wrapper--finder_filters--filters {
                    margin: 0;
                }

                .finder__results__summary {
                    margin: 0;
                }

                .finder__results__list {
                    @include x-column-grid(20);

                    .finder__results__card {
                        grid-column: 3 / -3;
                    }

                    &.finder__results__list--three-up {
                        display: flex;

                        .finder__results__card {
                            grid-column: unset;
                            width: calc(50% - #{$measure--basic});
                        }
                    }
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .listing {
                grid-column: 7 / span 18;

                .finder {
                    display: block;

                    .finder__query {
                        @include default-margin();
                    }

                    .wrapper--finder__filters--desktop
                        .finder__filters__heading {
                        @include default-margin();
                    }

                    .wrapper--finder__filters--desktop
                        .wrapper--finder_filters--filters {
                        @include default-margin();
                    }

                    .finder__results__summary {
                        margin: 0;
                    }

                    .finder__results__list {
                        @include content-grid();

                        .finder__results__card {
                            grid-column: 3 / -3;
                        }

                        &.finder__results__list--three-up {
                            display: flex;

                            .finder__results__card {
                                grid-column: unset;
                            }
                        }
                    }
                }
            }
        }
    }
}

.listing {
    .banner-text {
        background-color: $color-gray1;
        background: linear-gradient(
            180deg,
            $color-gray3 0%,
            $color-purewhite 100%
        );
        color: $color-nearblack;
        padding-bottom: $measure--large1;
        padding-top: $measure--small2;
    }

    .finder__query {
        margin-top: -#{$measure--medium7};
    }

    .wrapper--listing__content {
        @include default-padding();

        background-color: $color-pink90;
        color: $color-purewhite;
        margin-top: $measure--small2;
        padding-bottom: $measure--small2;
        padding-top: $measure--basic;

        .card--contact {
            border-top: 0;

            .icon {
                color: $color-purewhite;
            }

            a {
                @include anchor-inline(
                    $color-gray4,
                    $color-gray4,
                    $color-gray11,
                    $color-outline--dark
                );
            }
        }
    }

    @include media('<small', '>=tablet') {
        .finder__results__list--three-up {
            .card--news a {
                flex-direction: column;

                img {
                    width: 100%;
                }
            }
        }
    }

    @include media('<tablet') {
        .wrapper--finder__filters--mobile[data-open='false'] {
            margin-top: -#{$measure--medium7};
        }

        .finder__query ~ .wrapper--finder__filters--mobile[data-open='false'] {
            margin-top: unset;
        }
    }

    @include media('>=tablet') {
        .wrapper--finder__filters--desktop {
            margin-top: -#{$measure--medium7};
        }

        .finder__query ~ .wrapper--finder__filters--desktop {
            margin-top: unset;
        }
    }
}
